import api from "../../api";
// initial state

const state = {
  list: [] // 
  //   self_id: session.his_id() || null
};

// getters
const getters = {};

// actions
const actions = {
  // 
  getLikedList({ commit }, { self_id }) {
    // return;
    return api("user/find", {
      id: self_id,
      with: [
        {
          relation: "belongs_to_many",
          model: "mepo"
        }
      ]
    }).then(r => {
      //   console.log(r.data);
      commit("setLikedList", r.data.$mepo || []);
    });
  }
};

// mutations
const mutations = {
  // 
  setLikedList(state, list) {
    state.list = list;
  },
  setTotal(state, total) {
    state.total = total;
  }
};

const namespaced = true;

export default {
  namespaced,
  state,
  getters,
  actions,
  mutations
};
